import React from "react";
import { exploreInfo, itemImages } from "../ExploreList";
import { Container, Image, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import "../profPic.png";

const ExploreItem = ({ src, name, title, price, bid }) => {
  return (
    <div className="d-grid justify-content-center expCSS">
      <Image src={itemImages(src).default} className="expImg" fluid />
      <div className="expInfo">
        <h6>{name}</h6>
        <h6>{title}</h6>
        <h6>{price}</h6>
        <h6>{bid}</h6>
      </div>
    </div>
  );
};

export const ExploreList = () => {
  return (
    <div style={{ backgroundColor: "#F0A500" }}>
      <Container
        style={{ marginTop: "40px", paddingTop: "25px", paddingBottom: "30px" }}
      >
        <h3 style={{ marginBottom: "-10px" }}>Explore NFTs</h3>
        <Row xxl={6} xl={6} lg={5} md={4} sm={3} xs={2}>
          {exploreInfo.map((item) => {
            return (
              <Col style={{ marginTop: "15px" }}>
                <Link to="/" className="text-decoration-none">
                  <ExploreItem key={item.id} {...item}></ExploreItem>
                </Link>
              </Col>
            );
          })}
        </Row>
        <Row style={{ marginTop: "30px" }}>
          <Col lg={4} md={3} sm={3} xs={2}></Col>

          <Col className="d-flex justify-content-center ">
            <Link to="/" className="text-decoration-none seeMore">
              See More
            </Link>
          </Col>
          <Col lg={4} md={3} sm={3} xs={2}></Col>
        </Row>
      </Container>
    </div>
  );
};
