import React from "react";
import { Link } from "react-router-dom";
import { popularInfo, itemImages } from "../PopularList";
import {
  Container,
  Image,
  Row,
  Col,
  Dropdown,
  DropdownButton,
} from "react-bootstrap";

const Popular = ({ id, src, name, sold }) => {
  return (
    <div className="justify-content-around d-flex align-items-center popCSS">
      <h6>{id}</h6>
      <Image
        src={itemImages(src).default}
        alt=""
        roundedCircle
        className="popImg"
      />
      <div>
        <h6>{name}</h6>
        <h6>{sold}</h6>
      </div>
    </div>
  );
};

const Buttons = () => {
  return (
    <div className="d-flex align-items-start">
      <Col>
        <h3>Popular Sellers</h3>
      </Col>
      <Col className="d-flex justify-content-end align-items-center">
        <Link to="/" className="text-decoration-none viewAll">
          View All
        </Link>

        <DropdownButton id="dropdownToggleCSS" title="Toggle Button">
          <Dropdown.Item href="#" id="dropdownMenuCSS">
            A
          </Dropdown.Item>
          <Dropdown.Item href="#" id="dropdownMenuCSS">
            B
          </Dropdown.Item>
          <Dropdown.Item href="#" id="dropdownMenuCSS">
            C
          </Dropdown.Item>
          <Dropdown.Item href="#" id="dropdownMenuCSS">
            D
          </Dropdown.Item>
        </DropdownButton>
      </Col>
    </div>
  );
};

export const PopularList = () => {
  return (
    <Container style={{ marginTop: "25px" }}>
      <Buttons />
      <Row md={2}>
        {popularInfo.map((popularInfo) => {
          return (
            <Col
              xxl={2}
              xl={2}
              lg={3}
              md={4}
              sm={6}
              xs={6}
              className="d-felx align-self-end"
              style={{ marginTop: "5px" }}
            >
              <Link to="/" className="text-decoration-none">
                <Popular key={popularInfo.id} {...popularInfo}></Popular>
              </Link>
            </Col>
          );
        })}
      </Row>
    </Container>
  );
};
